		width: 32%;
		margin-bottom: 0;
	}
} ;

#contact {
	background-color: var(--dark03);
}

.contact {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	max-width: 900px;
}

form {
	width: 100%;
}

.form-element {
	display: flex;
	margin-bottom: 1.5em;
	flex-direction: column;
}

label {
	margin-bottom: 0.5em;
	font-size: 1.2rem;
	align-self: start;
	letter-spacing: 0.1em;
	font-weight: 200;
}

input,
textarea {
	flex-grow: 1;
	background-color: transparent;
	border: none;
	max-width: 100%;
	color: var(--light02);
	padding: 0.6em;
	border: 1px solid var(--light03);
	border-radius: 0.2em;
	font-family: 'Roboto Mono', monospace;
	font-size: inherit;
	outline: none;
}

input:focus,
textarea:focus {
	border-color: var(--yellow);
	background-color: var(--dark01);
}

textarea {
	resize: vertical;
	min-height: 8em;
	max-height: 26em;
}

button[type='submit'] {
	background-color: transparent;
	border: 1px solid var(--yellow);
	border-radius: 0.2em;
	padding: 0.6em 2em;
	color: var(--yellow);
	outline: none;
	transition: color 0.3s ease, background-color 0.3s ease;
	cursor: pointer;
	font-size: inherit;
	font-family: inherit;
	letter-spacing: 0.05em;
	width: 8em;
	margin-right: 1em;
}

button[type='submit']:disabled,
button[type='submit']:hover:disabled {
	-webkit-filter: grayscale(1) brightness(0.9);
	        filter: grayscale(1) brightness(0.9);
	cursor: default;
}

button[type='submit']::selection {
	color: var(--light01);
}

button[type='submit']:hover,
button[type='submit']:focus {
	background-color: var(--yellow);
	color: var(--dark03);
}

.success,
.error {
	display: inline-block;
	margin-top: 0.6em;
	color: var(--light04);
	white-space: nowrap;
}

.error {
	color: tomato;
}

@media screen and (min-width: 700px) {
	.form-element {
		flex-direction: row;
	}
	label {
		width: 20%;
	}
	button[type='submit'] {
		margin-left: 20%;
	}
}

#footer {
	background-color: var(--dark01);
}

.footer {
	width: 90%;
	margin: auto;
	padding: 2em 0;
	text-align: center;
}

.socials {
	display: flex;
	list-style: none;
	justify-content: space-between;
	max-width: 20em;
	margin: auto;
	margin-top: 2em;
}

.social a {
	color: var(--dark05);
	transition: color 0.3s ease;
	outline: none;
}

.social a:hover,
.social a:focus {
	color: var(--yellow);
}

.social span {
	display: none;
}

.footer img {
	height: 3em;
	width: 3em;
	-webkit-filter: grayscale(1);
	        filter: grayscale(1);
	opacity: 0.2;
	margin-bottom: 1em;
}

.copyright {
	color: var(--dark05);
	line-height: 1.5em;
}

.top {
	order: 4;
	margin: auto;
	margin-top: 1em;
	padding: 1em;
	display: inline-block;
	cursor: pointer;
	height: 4em;
	width: 4em;
	border-radius: 2em;
	transition: background-color 0.3s ease;
	transform: scale(0.8);
	outline: none;
}

.top:hover {
	background-color: var(--dark02);
}

.top-icon {
	color: var(--dark05);
	animation: move 1s linear infinite alternate;
}

@keyframes move {
	from {
		transform: translateY(-15%);
	}
	to {
		transform: translateY(15%);
	}
}

@media screen and (min-width: 800px) {
	.footer {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	.footer img {
		order: 1;
		height: 2em;
		width: 2em;
		margin: 0;
	}

	.socials {
		order: 2;
		margin: 0;
		width: 20em;
	}

	.copyright {
		order: 3;
		width: 100%;
		margin-top: 2em;
	}
	.top {
		order: 4;
	}
}

:root {
	--dark01: #141414;
	--dark02: #202020;
	--dark03: #292929;
	--dark04: #343434;
	--dark05: #5f5f5f;
	--light01: #fff;
	--light02: #e9e9e9;
	--light03: #bfbfbf;
	--light04: #b8b8b8;
	--gray: #909090;
	--yellow: #e1c300;
	--cnf-fg: #b0a990;
	--cnf-bg: #202030;
	--mors01: #263238;
	--mors02: #303046;
	--teal: #008080;
	--shadow-clr: #00000065;
	--card-shadow-clr: #00000045;
}

::selection {
	color: #e1c300;
	color: var(--yellow);
}

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	scroll-behavior: smooth;
	-webkit-scroll-snap-type: y proximity;
	    -ms-scroll-snap-type: y proximity;
	        scroll-snap-type: y proximity;
}
body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI',
		Roboto, Oxygen, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	background-color: #202020;
	background-color: var(--dark02);
	color: #fff;
	color: var(--light01);
	min-height: 100vh;
	-webkit-scroll-snap-type: y proximity;
	    -ms-scroll-snap-type: y proximity;
	        scroll-snap-type: y proximity; /*for safari users*/
}

section,
footer {
	scroll-snap-align: start;
}

a {
	text-decoration: none;
	color: #fff;
	color: var(--light01);
}

.contained {
	width: 90%;
	min-height: 100vh;
	padding-top: 4em;
	margin: auto;
}

.regular-text {
	letter-spacing: 0.1em;
	line-height: 1.5em;
}

/* onScreen animations */

section,
footer {
	opacity: 0;
	transition: opacity 0.5s ease;
}

.onScreen {
	opacity: 1;
}

/* scrollbar */

::-webkit-scrollbar {
	width: 0.4em;
}

::-webkit-scrollbar-track {
	background-color: #343434;
	background-color: var(--dark04);
}

::-webkit-scrollbar-thumb {
	background-color: #909090;
	background-color: var(--gray);
	cursor: pointer;
}

::-webkit-scrollbar-thumb:hover {
	background-color: #bfbfbf;
	background-color: var(--light03);
}

::-webkit-scrollbar-thumb:active {
	background-color: #e1c300;
	background-color: var(--yellow);
}

/* remove scroll snap on small devices*/

@media screen and (max-width: 600px) {
	html,
	body {
		-webkit-scroll-snap-type: none;
		    -ms-scroll-snap-type: none;
		        scroll-snap-type: none;
	}
}

